import React from 'react';

interface FormFieldProps {
  label: string;
  htmlFor?: string;
  helper?: string;
  error?: string | null;
  children: React.ReactNode;
}

export default function FormField({ label, htmlFor, helper, error, children }: FormFieldProps) {
  const errorId = htmlFor ? `${htmlFor}-error` : undefined;

  // If the child is a valid React element, clone it and inject aria props
  const childWithA11y = React.isValidElement(children)
    ? React.cloneElement(children as any, {
        id: (children as any).props.id || htmlFor,
        ...(errorId ? { 'aria-describedby': errorId } : {}),
        ...(error ? { invalid: true } : {}),
      })
    : children;

  return (
    <div className="mb-5">
      <label htmlFor={htmlFor} className="block text-sm font-medium text-slate-700 mb-2">
        {label}
      </label>
      <div>{childWithA11y}</div>
      {helper && <p className="text-xs text-slate-500 mt-1">{helper}</p>}
      {error && <p id={errorId} className="text-xs text-red-600 mt-1">{error}</p>}
    </div>
  );
}
